.App {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-self: center;
  align-items: center;
  background: rgb(3, 0, 10);
  height: 130vh;
}

h2 {
  color: white;
  animation: 0.4s ease-in forwards;
  animation-name: listIn;
}

.count {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
}

.balance{
  margin: 0 auto;
}

.formfield {
  border: none;
  border-bottom: solid 2px rgb(65, 65, 65);
  outline: none;
  margin: 20px 0px;
  padding-left: 10px;
  background: transparent;
  width: 80%;
  color: white;
  font-size: 15px;
}

span {
  position: absolute;
  top: 0;
  left: 0;
  background: red;
  padding: 10px;
}

.formfield:focus {
  background: transparent
}

.btnForm {
  border: none;
  outline: none;
  height: 40px;
  width: 100px;
  border-radius: 4px;
}

.transactionsapp {
  display: flex;
  width: 100%;
}

.transactionsExpense,
.transactionsIncome {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 50%;
}

.transactionsExpense {
  margin: 0px 50px;
 
}

form {
  display: flex;
  flex-direction: column;
  width: 90%;
  margin-bottom: 30px;
}

::placeholder {
  color: white;
}

.btnIncome {
  background: green;
}

.btnIncome:hover {
  color: green;
  border: green solid 2px;
}

.btnExpense:hover {
  color: red;
  border: red solid 2px;
}

.btnExpense {
  background: red;
}

.btnForm {
  transition: ease-in 0.3s;
  font-size: 15px;
  transform-style: preserve-3d;
  perspective: 1000px;
}

.btnForm:hover {
  animation: 0.2s ease-in forwards;
  animation-name: hovered;
  cursor: pointer;
  background: bisque;
  transform-style: preserve-3d;
  perspective: 1000px;
}

.btnRemove {
  border: none;
  outline: none;
  width: 60px;
  height: 30px;
  padding: 5px;
  background: red;
  transition: ease-in 0.3s;
}

.btnRemove:hover {
  cursor: pointer;
}

.btnRemove:hover > i{
  color: white;
}

i {
  font-size: 18px;
  transition: ease-in 0.2s;
}


td {
  text-transform: capitalize;
  font-size: 18px;
  width: 250px;
  height: 30px;
  margin: 0px;
  
}

tr {
  color: white;
  animation: 0.4s ease-in forwards;
  animation-name: listIn;
}


