@media only screen and (min-width: 321px) and (max-width: 415px) {

  .App {
    height: 0%;
  }
  .transactionsapp {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-wrap: wrap;
    padding-bottom: 60px;
    
  }
  .transactionsIncome {
    padding: 15px 0px;
    margin: 0px 50px;
  }

  .transactionsExpense, 
  .transactionsIncome {
    width: 85%;
  }

  .expenses {
    display: flex;
    justify-content: center;
  }

  form {
    margin: 50px 0px;
    width: 100%
  }

  .formfield {
    width: 100%;
  }

  th {
    white-space: nowrap;
  }

  td {
    font-size: 15px;
  }

  i {
    font-size:12px
  }

  span {
    display: none;
  }
}


